import request from '@/util/request.js'

export function get_err(obj) {
    return request ({
        url: '/app1/err'+obj,
        method: 'get',
        params: ''
    })
}

export function patch_err(obj) {
    return request({
        url: '/app1/err',
        method: 'patch',
        data: obj
    })
}





export function get_merchants() {
    return request({  //request
        url: '/home/merchants',// requestbaseUrl
        method: 'get',//
        data: ''//
    })
}


